<template>
    <div>
      <div class="search-container">
        <input v-model="search" placeholder="Search questions..." />
        <button v-if="search" @click="clearSearch" class="clear-button">X</button>
      </div>
      <div v-if="filteredQuestions.length === 0">
        <p>Brak takiego pytania</p>
      </div>
      <QuestionItem 
        v-for="(item, index) in filteredQuestions" 
        :key="index" 
        :item="item" 
        :index="index" 
      />
    </div>
  </template>
  
  <script>
  import QuestionItem from './QuestionItem.vue';
  
  export default {
    name: 'QuestionList',
    components: {
      QuestionItem,
    },
    props: {
      questions: Array,
    },
    data() {
      return {
        search: '',
      };
    },
    computed: {
      filteredQuestions() {
        return this.questions.filter(q => 
          q.question.toLowerCase().includes(this.search.toLowerCase())
        );
      },
    },
    methods: {
      clearSearch() {
        this.search = '';
      }
    }
  };
  </script>
  
  <style>
  .search-container {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
  }
  
  input {
    width: 100%;
    padding: 12px 40px 12px 20px; /* Adjust padding to make room for the clear button */
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }
  
  input:focus {
    border-color: #42b983;
  }
  
  .clear-button {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;
    color: #ccc;
  }
  
  .clear-button:hover {
    color: #42b983;
  }
  
  p {
    font-size: 18px;
    color: #215a41;
  }
  </style>
  