<template>
    <div>
      Autoryzowanie...
    </div>
  </template>
  
  <script>
  export default {
    created() {
      this.$auth.authenticate('github', this.$route.query).then(() => {
        this.$router.push({ path: '/' });
      }).catch((err) => {
        console.error('Error authenticating:', err);
      });
    }
  };
  </script>
  